section.section {
  padding-top: 2em;
}

.resource-container {
  display: flex;
  flex-wrap: nowrap;
}

.filter-container {
  height: 0;
  overflow: hidden;
  transition: height 0.3s;
}
@media only screen and (min-width: 50em) {
  .filter-container {
    height: 100%;
    flex: 0 0 18.75rem;
  }
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  grid-template-rows: auto;
  height: fit-content;
  width: 100%;
}
@media only screen and (min-width: 50em) {
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(22rem, 1fr));
    grid-template-rows: auto;
  }
}

@media only screen and (min-width: 50em) {
  .container {
    padding-left: 2em;
  }
}

.browse-mobile-btn-wrapper {
  display: flex;
  flex-wrap: nowrap;
  background-color: #fff;
  justify-content: flex-end;
  position: sticky;
  right: 1.25em;
  top: 3.5rem;
  transition: 0.1s;
  width: 100%;
  z-index: 10;
}
@media only screen and (min-width: 50em) {
  .browse-mobile-btn-wrapper {
    display: none;
  }
}

.browse-mobile-btn {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  font-family: "montserratregular", Helvetica Neue, Helvetica, sans-serif;
  font-size: 1.2rem;
  height: 3rem;
  margin-bottom: 1em;
  margin-top: 0.6em;
  padding: 0.25em 0.5em;
}
.browse-mobile-btn .fas {
  color: #f1f1f1;
  font-size: 1.5rem;
  margin-right: 0.5em;
}
.browse-mobile-btn img {
  margin-right: 0.6em;
  width: 2.6rem;
}

.total-results {
  font-size: 0.9rem;
  text-align: right;
}

.active-filters-wrapper h6 {
  display: none;
  font-size: 0.8rem;
  font-weight: bold;
}

.active-filters {
  display: flex;
  flex-wrap: wrap;
  margin-left: 0;
}
.active-filters li {
  list-style-type: none;
  margin: 0.25em;
}

.active-filter-tag {
  background: transparent;
  border: 1px solid #40476D;
  border-radius: 2px;
  color: #40476D;
  font-family: "open_sansregular", Helvetica Neue, Helvetica, sans-serif;
  font-size: 0.8rem;
  padding: 0.5em 1.8em 0.5em 0.5em;
  position: relative;
}
.active-filter-tag:hover {
  background: transparent;
  border: 1px solid #40476D;
  color: #40476D;
}

.close-active-filter-tag {
  display: block;
  position: absolute;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 10px;
  height: 10px;
  font-size: 0;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.close-active-filter-tag::before {
  position: absolute;
  display: block;
  left: 0;
  top: 5px;
  width: 100%;
  height: 1px;
  background-color: #40476D;
  content: "";
  transform: rotate(45deg);
}
.close-active-filter-tag::after {
  position: absolute;
  display: block;
  left: 0;
  top: 5px;
  width: 100%;
  height: 1px;
  background-color: #40476D;
  content: "";
  transform: rotate(-45deg);
}

.text-search-options h5:last-of-type {
  margin-top: 0.5em;
}
.text-search-options #reset-btn {
  margin-top: 1em;
}

.search-text-wrapper {
  display: flex;
  flex-wrap: nowrap;
}

input#search[type=text] {
  flex-grow: 2;
}

#accord {
  margin-top: 1.5em;
}

.accord-trigger {
  background-color: transparent;
  border: none;
  color: #2b2b2b;
  display: flex;
  font-style: "open_sansregular", Helvetica Neue, Helvetica, sans-serif;
  line-height: inherit;
  text-align: left;
}
.accord-trigger:hover {
  background-color: transparent;
  border: none;
}

.hz-icon {
  align-self: center;
  margin-left: 0.5em;
}

.facet-count {
  align-self: center;
  font-size: 0.95rem;
  margin-left: 0.25em;
}

.accordion-section {
  border-bottom: 1px solid #c1c1c1;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.1rem;
  margin-bottom: 0;
  padding: 0.5em;
}
.accordion-section:first-of-type {
  border-top: 1px solid #c1c1c1;
}
.accordion-section .fas {
  margin-right: 1em;
}

.accordion-section.active {
  background: #f1f1f1;
  border-top: 1px solid #c1c1c1;
}

.filter-panel {
  display: none;
  max-height: unset;
}
.filter-panel ul.option {
  list-style: none;
  margin: 0;
  position: relative;
}
.filter-panel ul.option ul.option {
  border-left: 1px solid #c1c1c1;
  padding-left: 1em;
}
.filter-panel ul.option input[type=checkbox] {
  margin-right: 0.5em;
}

.filter-panel.active {
  display: block;
}

.coursesource-card {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  box-shadow: 0px 0px 5px 1px #CAC4CE;
  margin: 1.25em 1em;
  position: relative;
}
.coursesource-card h5 {
  margin-top: 0.4em;
  word-break: break-word;
}

.coursesource-card-img {
  display: flex;
  flex-wrap: nowrap;
  position: relative;
}
.coursesource-card-img img {
  display: block;
  width: 100%;
  height: 15rem;
  margin: 0 auto;
  object-fit: cover;
}

.coursesource-card-contents {
  padding: 0 0.5em;
}
.coursesource-card-contents .keyword,
.coursesource-card-contents .author {
  font-size: 0.9rem;
  border-right: 1px solid #c1c1c1;
  display: inline-block;
  padding: 0 0.5em;
}
.coursesource-card-contents .keyword:first-of-type,
.coursesource-card-contents .author:first-of-type {
  padding-left: 0;
}
.coursesource-card-contents .keyword:last-of-type,
.coursesource-card-contents .author:last-of-type {
  border-right: none;
}

.pub-keywords,
.pub-version,
.pub-fork {
  font-size: 0.9rem;
}

.pub-version {
  margin-bottom: 0;
}

.pub-fork {
  margin-top: 0;
}

.meta-panel {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-evenly;
  margin-top: auto;
  padding: 0.5em;
}
.meta-panel .divider {
  background-color: #c1c1c1;
  height: 3.125rem;
  width: 1px;
}

.meta-item {
  text-align: center;
}
.meta-item p {
  font-size: 0.7rem;
  margin: 0.5 0;
}

.coursesource-card-courses {
  font-size: 0.9rem;
}
.coursesource-card-courses span {
  border-right: 1px solid #f1f1f1;
  padding: 0 0.5em;
}
.coursesource-card-courses span:last-of-type {
  border-right: none;
}
.coursesource-card-courses img {
  height: 0.9rem;
  margin-right: 0.25em;
  width: auto;
}

.coursesource-card-keywords {
  margin-bottom: 0.5em;
}
.coursesource-card-keywords .tag-icon svg {
  height: 0.7rem;
  width: auto;
}
.coursesource-card-keywords .coursesource-keywords {
  border-right: 1px solid #f1f1f1;
  font-size: 0.8rem;
  font-style: oblique;
  padding: 0 0.5em;
}
.coursesource-card-keywords .coursesource-keywords:last-of-type {
  border-right: none;
}

.pagination,
p.warning {
  grid-column: 1/span 1;
  grid-row: auto;
}
@media only screen and (min-width: 69.75em) {
  .pagination,
  p.warning {
    grid-column: 1/span 2;
    grid-row: auto;
  }
}
@media only screen and (min-width: 92.875em) {
  .pagination,
  p.warning {
    grid-column: 1/span 3;
    grid-row: auto;
  }
}

@supports (display: grid) {
  .resource-card {
    width: auto;
  }
}
.mobile-active {
  background-color: #fff;
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.3);
  height: auto;
  padding: 1em;
  position: fixed;
  left: 0;
  right: 0;
  top: 8.375rem;
  width: 100%;
  z-index: 101;
}